import { createRouter, createWebHashHistory } from 'vue-router';

import homePage from './components/HomePage.vue';
import blogPost from './components/BlogPost.vue';
import about from './components/AboutSite.vue';
import addPage from './components/AddBlog.vue';
import register from './components/auth/AddUsr.vue';
import login from './components/auth/LogIn.vue';
import admin from './components/auth/AdminPage.vue';
import test from './components/TestPage.vue';
import blogshow from './components/BlogShow.vue';
import excellent from './components/ExcellEnt.vue';
import realLib from './components/RealLibrary.vue';
import editPage from './components/EditPage.vue';
import AaA from './components/AaA.vue';

const router = createRouter({
  history: createWebHashHistory(),
  routes: [
    { path: "/", component: homePage },
    { path: "/blogs", component: blogPost},
    { path: "/about", component: about},
    { path: "/addPage", component: addPage},
    { path: "/register", component: register},
    { path: "/login", component: login},
    { path: "/admin", component: admin},
    { path: "/test", component: test},
    { path: "/show/:id", component: blogshow, name: 'Show'},
    { path: "/excellent", component: excellent},
    { path: "/realLib", component: realLib},
    { path: "/show/:id/edit", component: editPage},
    { path: "/a", component: AaA},
  ],
});

export default router;
